import React from 'react';
import styles from 'src/styles/Header.module.css';
import Core from 'src/Core/Core.js';
import Config from 'src/Config.js';
import { Link } from 'gatsby';

class HeaderInfo extends React.Component {
    click() {
        document.location.href = Core.path('/');
    };

    render() {
        return (
            <div className={styles.info} onClick={this.click}>
                <div className={styles.iconWrapper}>
                    <img src={Config.ProjectData.AvatarURL} alt="" className={styles.icon} />
                </div>
                <h1 className={styles.name}>
                    {Config.ProjectData.Name}
                </h1>
            </div>
        );
    };
};

class HeaderContent extends React.Component {
    render() {
        return (
            <div className={styles.content}>
                <nav>
                    {this.props.children}
                </nav>
            </div>
        );
    };
};

class HeaderContentItem extends React.Component {
    render() {
        return (
            <Link className={styles.e} to={this.props.href} title={this.props.title}>
                <h1>
                    {this.props.children}
                </h1>
            </Link>
        );
    };
};

class Header extends React.Component {
    render() {
        return (
            <div className={styles.Header}>
                <HeaderInfo />
                <HeaderContent>
                    <HeaderContentItem href="/" title="Главная страница">
                        Главная
                    </HeaderContentItem>
                    <HeaderContentItem href="/commands/" title="Команды бота">
                        Команды
                    </HeaderContentItem>
                    <HeaderContentItem href="/developers/" title="Разработчики">
                        Devs
                    </HeaderContentItem>
                    <HeaderContentItem href="/features/" title="Возможности бота">
                        Возможности
                    </HeaderContentItem>
                </HeaderContent>
            </div>
        );
    };
};

export default Header;